import { NavLink } from 'react-router-dom';
import { Formik, Form } from 'formik';
import * as Yup from 'yup';
import TextImport from './TextInput';

function Register({ onRegister }) {
  return (
    <section className="auth">
      <h1 className="auth__title"></h1>

      <Formik
        initialValues={{
          email: '',
          password: '',
        }}
        validationSchema={Yup.object({
          email: Yup.string()
            .email('    ')
            .required('   '),
          password: Yup.string()
            .min(8, '     8 ')
            .required('   '),
        })}
        onSubmit={(values, { setSubmitting }) => {
          onRegister({ email: values.email, password: values.password });

          setSubmitting(false);
        }}
      >
        <Form className="auth-form">
          <fieldset className="auth-form__fieldset">
            <TextImport name="email" type="text" placeholder="Email" />
            <TextImport name="password" type="password" placeholder="" />
          </fieldset>

          <button className="auth-form__button" type="submit">
            
          </button>
        </Form>
      </Formik>
      <p className="auth__text">
         ?&nbsp;
        <NavLink className="auth__link" to="/sign-in">
          
        </NavLink>
      </p>
    </section>
  );
}

export default Register;
